body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
	text-decoration: underline;
	color: #0AA0D0;
}

li {
	text-align: left;
	line-height: 1.7em;
}

h2 {
	text-align: left;
}

* {
	font-family: "Roboto",sans-serif;
}

p {
	line-height: 1.3em;
}

#left {
	text-align: center;
	float:left;
	width: 40%;
}

#right {
	float: left;
	width: 60%;
}

#raghav {
	height: 10em;
	width: 10em;
	border-radius: 50%;
	display: inline-block;
}

#qualifications {
	margin-top: 0.5em;
	display: inline-block;
}

#experience {
	margin-top: 0.5em;
	display: inline-block;
}

#online {
	margin-top: 1em;
}

#projectContent {
	display: inline-block;
	margin-top: 0;
}

#container {
	margin-top: 0.75em;
	margin-left: 2vw;
	margin-right: 2vw;
}

#header {
	text-align: left;
}


.logoContainer {
	text-align: center;
}

.logoImage {
	max-height: 2.1em;
	max-width: 2.1em;
}

.rightContent {
	line-height: 1.7em;
	text-align: left;
}

.myLine {
	width:15%; 
	border: 1px solid #0AA0D0;
}
